@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
*{box-sizing:border-box;margin:0;padding:0;font-family:'Roboto', sans-serif;line-height:1.5}

:root{--text-faded: #3B3B3B;--color-list: #f4f4f4}

@media (min-width: 1025px) and (max-width: 1920px){.container{width:80%;margin:0 auto}}@media (min-width: 1921px){.container{width:1800px;margin:0 autO}}

.button{padding:14px 25px;display:block;border:none;box-shadow:0 0 6px rgba(0,0,0,0.1);cursor:pointer;font-weight:600;font-size:14px}.button-primary{background:black;color:#FAFAFA}.button-outline{border:1px solid black;background:none}.button-transparent{background:None;box-shadow:none}.button-icon{width:100%;display:flex;align-items:center;justify-content:center}.button-icon svg{margin-left:10px;font-size:32px}.button-long-border{display:flex;align-items:center}.button-long-border .button-border{display:block;height:1px;width:100%;background:#d8d8d8}.button-long-border-button-wrap{width:100%;border:None;background:none;font-size:16px;display:flex;align-items:center;justify-content:center;font-weight:600}.button-long-border-icon{font-size:16px;margin-left:16px}.button-long-border-content-wrap{display:flex;align-items:center;justify-content:center;width:100%}

.navbar{width:100%;box-shadow:0 0 15px rgba(0,0,0,0.2);height:70px}.navbar>.container{display:flex;justify-content:space-between;align-items:center;height:100%}.navbar-list-item{text-decoration:none;list-style:None}.navbar-profile-img{margin-left:16px}.navbar-logo{max-height:50px}.navbar-list-link{color:black;text-decoration:none;font-weight:600}.navbar-items-list{display:flex;align-items:center}.navbar-items-list .navbar-list-item:nth-child(2){margin:0 30px}

.input-wrap{display:flex;box-shadow:0 0 7px rgba(0,0,0,0.1);align-items:center;cursor:pointer}.input-wrap .input-icon{font-size:24px;padding:0  10px;position:absolute}.input-wrap .input{padding-left:44px;border:none;height:50px;font-weight:500;outline:none;width:100%}

.login-modal{background:white;padding:40px 16px}.login-modal .hero-image-wrap{width:50%}.login-modal .hero-image-wrap .hero-image{width:100%;height:100vh}.login-modal-button-wrap{display:flex}.login-modal-button-wrap .button:last-child{margin-left:16px}.login-modal .input-wrap{min-width:350px;margin-bottom:16px}.sign-up-container{padding:40px 32px;min-width:400px;background:White}.sign-up-container .input-wrap{margin-bottom:24px}

.fullscreen-modal{position:fixed;top:0px;left:0px;height:100vh;width:100vw;background:rgba(0,0,0,0.3);display:flex;align-items:center;justify-content:center}.fullscreen-modal-content{position:relative}.fullscreen-modal .icon-button{position:absolute;top:10px;right:10px}

.icon-button{background:black;border-radius:50%;color:white;border:none;display:flex;align-items:center;justify-content:center;cursor:pointer}.icon-button>svg{font-size:24px;color:White}

.user-profile{height:60px;width:60px;border-radius:50%;object-fit:cover;box-shadow:0 0 7px rgba(0,0,0,0.2)}

.video-thumbnail{cursor:pointer;position:relative}.video-thumbnail>a{height:100%;width:100%;text-decoration:none}.video-thumbnail-img-wrap{position:relative}.video-thumbnail-img{height:200px;box-shadow:0 0 15px rgba(0,0,0,0.2);object-fit:cover;width:100%}.video-thumbnail-text{margin-top:8px;color:black;text-decoration:none;font-weight:600}.video-thumbnail-user{margin-top:8px;font-size:14px;color:var(--text-faded)}.video-thumbnail-popup-play{color:White;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);z-index:100}.video-thumbnail-popup-play-icon{font-size:40px}.video-thumbnail-popup-play-title{font-weight:700;font-size:18px}

.video-section{margin:56px 0px}.video-section-title{font-size:24px;font-weight:700;margin-bottom:40px}.video-section-view-more{margin-top:24px}.video-section-videos{display:flex;justify-content:space-between}.video-section-videos .video-thumbnail{width:23%;max-width:350px}

.video-comment{cursor:pointer}.video-comment-username{font-size:16px;font-weight:600}.video-comment-profile-image{margin-right:16px}.video-comment-info{display:flex;align-items:center}.video-comment-timestamp{margin-top:4px;color:var(--text-faded) !important;font-size:14px}.video-comment-comment{margin-top:16px}

.textarea{border:none;resize:none;padding:5px;font-size:16px;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.comment-modal{display:flex;flex-direction:column;justify-content:space-between;padding:20px 20px;background:White;position:relative}.comment-modal-close{position:absolute;top:10px;right:10px;cursor:pointer}.comment-modal .textarea{width:100%;height:100%}.comment-modal .textarea-wrap{min-height:300px;width:100%}.comment-modal-user-text-wrap{display:flex}.comment-modal-user-text-wrap .user-profile{margin-right:16px}.comment-modal .textarea{outline:none}

.video-player-page{display:flex;margin-top:50px;height:calc(100vh - 120px);justify-content:space-between}.video-player-page .video-wrap{width:50%}.video-player-page .video-wrap video{display:block;margin-bottom:24px}.video-player-page .video-comment-list{height:calc(100vh - 120px);background:var(--color-list);overflow:scroll;width:45%;padding:32px 16px}.video-player-page .video-comment-list .video-comment{margin-bottom:32px}.video-info{position:relative}.video-info-user{margin-top:24px;font-size:18px;font-weight:500;margin-bottom:16px}.video-info-upper{padding-bottom:16px;border-bottom:1px solid var(--text-faded)}.video-info-description{color:var(--text-faded);max-width:600px}.video-info-views,.video-info-createdAt{font-size:14px;color:var(--text-faded);font-weight:500;margin-right:16px}.video-info .icon-button{position:absolute;right:0px;top:0px}.video-comment-general-list{margin:100px 0px}.video-comment-general{position:relative;margin-bottom:50px;padding-bottom:20px}.video-comment-general-user{font-size:16px;font-weight:500;margin-bottom:8px}.video-comment-general-text{color:var(--text-faded);max-width:500px}.video-comment-general-created-at{font-size:14px;position:absolute;bottom:0;right:0}

.create-comment-modal{background:White;min-width:600px;max-width:600px;width:100%;padding:32px 24px}.create-comment-modal-title{font-size:32px;font-weight:600;margin-bottom:32px}.create-comment-modal-button-wrap{display:flex}.create-comment-modal-button-wrap .button{margin:0 20px}.create-comment-modal-button-wrap{display:flex;margin-top:24px}

